<template>
  <v-card class="mb-12" elevation="0">
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-row>
        <v-col>
          <v-alert shaped type="error" v-if="error">
            {{ error }}
          </v-alert>
        </v-col>
        <v-col cols="12">
          <v-card-title>
            Total : <v-chip class="ml-2">{{ total }} €</v-chip>
          </v-card-title>
        </v-col>
        <v-col cols="12">
          <v-card-title>
            Comptage des pièces :
            <v-chip class="ml-2">{{ totalPiece }} €</v-chip>
          </v-card-title>
        </v-col>
        <v-col cols="1" v-for="piece in caisseEspece.pieces" :key="piece.value">
          <v-text-field
            v-model="piece.nbr"
            :rules="Rules"
            :label="piece.label"
            required
          ></v-text-field>
        </v-col>

        <v-col cols="12">
          <v-card-title>
            Comptage des billets :
            <v-chip class="ml-2">{{ totalBillet }} €</v-chip>
          </v-card-title>
        </v-col>
        <v-col
          cols="1"
          v-for="billet in caisseEspece.billets"
          :key="billet.value"
        >
          <v-text-field
            v-model="billet.nbr"
            :rules="Rules"
            :label="billet.label"
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-btn color="primary" class="mr-4" @click="back()" text> retour </v-btn>
      <v-btn :disabled="!valid" color="primary" class="mr-4" @click="validate">
        Confirmer
      </v-btn>
    </v-form>
  </v-card>
</template>

<script>
export default {
  computed: {
    total() {
      return this.totalBillet + this.totalPiece;
    },
    totalPiece() {
      var total = 0;
      this.caisseEspece.pieces.forEach((element) => {
        if (element.nbr >= 0) {
          total += element.nbr * element.value;
        }
      });
      return total;
    },
    totalBillet() {
      var total = 0;
      this.caisseEspece.billets.forEach((element) => {
        if (element.nbr >= 0) {
          total += element.nbr * element.value;
        }
      });
      return total;
    },
  },
  data: () => ({
    valid: true,
    error: "",
    caisseEspece: {
      pieces: [
        {
          value: 0.01,
          nbr: "",
          label: "1 cent",
        },
        {
          value: 0.02,
          nbr: "",
          label: "2 cent",
        },
        {
          value: 0.05,
          nbr: "",
          label: "5 cent",
        },
        {
          value: 0.1,
          nbr: "",
          label: "10 cent",
        },
        {
          value: 0.2,
          nbr: "",
          label: "20 cent",
        },
        {
          value: 0.5,
          nbr: "",
          label: "50 cent",
        },
        {
          value: 1,
          nbr: "",
          label: "1 eur",
        },
        {
          value: 2,
          nbr: "",
          label: "2 eur",
        },
      ],
      billets: [
        {
          value: 5,
          nbr: "",
          label: "5 eur",
        },
        {
          value: 10,
          nbr: "",
          label: "10 eur",
        },
        {
          value: 20,
          nbr: "",
          label: "20 eur",
        },
        {
          value: 50,
          nbr: "",
          label: "50 eur",
        },
        {
          value: 100,
          nbr: "",
          label: "100 eur",
        },
        {
          value: 200,
          nbr: "",
          label: "200 eur",
        },
        {
          value: 500,
          nbr: "",
          label: "500 eur",
        },
      ],
    },
    Rules: [
      (v) => !!v || "Merci de compléter le champ",
      (v) => (v && v >= 0) || "le champs n'autorise que des chiffres positif",
    ],
  }),

  methods: {
    back() {
      this.$emit("back");
    },
    validate() {
      this.error = "";
      if (this.$refs.form.validate()) {
        if (this.total != 300) {
          return (this.error =
            "Merci de vérifier votre comptage, la somme ne correspond pas à 300€");
        }
        this.caisseEspece.totalPiece = this.totalPiece;
        this.caisseEspece.totalBillet = this.totalBillet;
        this.$emit("dataFondCaisse", this.caisseEspece);
        //this.$refs.form.reset();
      }
    },
    reset() {},
    resetValidation() {
      this.$refs.form.resetValidation();
    },
  },
};
</script>