<template>
  <v-card>
    <v-card-title>
      Montant Total de vos Cheque :
      <v-chip class="ml-2">{{ total }} €</v-chip>
    </v-card-title>

    <v-form ref="form" v-model="valid" lazy-validation>
      <v-row>
        <v-col cols="2">
          <v-text-field
            v-model="ncheque"
            :rules="nChequeRules"
            label="n° chèque"
            required
          ></v-text-field>
        </v-col>

        <v-col cols="2">
          <v-text-field
            v-model="montant"
            :rules="chequeRules"
            label="Montant chèque"
            required
          ></v-text-field>
        </v-col>
        <v-col cols="2">
          <v-select
            v-model="encaissement"
            :rules="selectRule"
            :items="items"
            label="mode d'encaissement"
          ></v-select>
        </v-col>
        <v-col cols="3" v-if="encaissement == 'sur facture'">
          <v-text-field
            v-model="justificatif"
            :rules="selectRule"
            label="N°Facture ou Nom Prénom + mail + tel"
            required
          ></v-text-field>
        </v-col>

        <v-col cols="2">
          <v-btn
            :disabled="!valid"
            color="primary"
            class="mr-4"
            @click="validate"
          >
            Ajouter
          </v-btn>
        </v-col>
      </v-row>
    </v-form>

    <v-card-text>
      <v-row>
        <v-col cols="4" v-for="(mount, index) in listCheque" :key="index">
          <div class="text-center">
            <v-chip close @click:close="deleteCb(index)">
              {{ mount.numero }} => {{ mount.montant }}€ -
              {{ mount.encaissement }}
              <div v-if="mount.justificatif">-{{ mount.justificatif }}</div>
            </v-chip>
          </div>
        </v-col>
      </v-row>
    </v-card-text>

    <v-card-actions>
      <v-btn color="primary" class="mr-4" @click="back()" text> retour </v-btn>
      <v-btn color="primary" class="mr-4" @click="confirm()"> Confirmer </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    items: ["en caisse", "sur facture"],
    montant: "",
    ncheque: "",
    encaissement: "",
    justificatif: "",
    selectRule: [(v) => !!v || "merci de choisir un encaissement"],
    chequeRules: [
      (v) => !!v || "un montant est requis",
      (v) => (v && v > 0) || "Le montant doit être supérieur à 0€",
    ],
    nChequeRules: [(v) => !!v || "un n° est requis"],
    listCheque: [],
  }),
  methods: {
    back() {
      this.$emit("back");
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.listCheque.push({
          numero: this.ncheque,
          montant: this.montant,
          encaissement: this.encaissement,
          justificatif: this.justificatif,
        });
        this.$refs.form.reset();
      }
    },
    deleteCb(index) {
      this.listCheque.splice(index, 1);
    },
    confirm() {
      const message = `Voulez vous vraiment confirmer le montant à ${this.total} € ?`;
      var res = confirm(message);
      if (res) {
        var cheque = {
          listCheque: this.listCheque,
          total: this.total,
          totalBureau: this.totalBureau,
          totalAcceuil: this.totalAcceuil,
        };
        this.$refs.form.reset();
        return this.$emit("dataCheque", cheque);
      }
    },
  },
  computed: {
    total() {
      var total = 0;
      if (this.listCheque.length > 0) {
        this.listCheque.forEach((item) => (total += parseFloat(item.montant)));
      }
      return total;
    },
    totalBureau() {
      var total = 0;
      var opBureau = this.listCheque.filter(
        (item) => item.encaissement == "sur facture"
      );

      if (opBureau.length > 0) {
        opBureau.forEach((item) => {
          total += parseFloat(item.montant);
        });
      }

      return total;
    },
    totalAcceuil() {
      var total = 0;
      var opAcceuil = this.listCheque.filter(
        (item) => item.encaissement == "en caisse"
      );

      if (opAcceuil.length > 0) {
        opAcceuil.forEach((item) => {
          total += parseFloat(item.montant);
        });
      }

      return total;
    },
  },
};
</script>