<template>
  <v-card>
    <v-card-title>
      Montant Total de vos Sortie :
      <v-chip class="ml-2">{{ total }} €</v-chip>
    </v-card-title>

    <v-form ref="form" v-model="valid" lazy-validation>
      <v-row>
        <v-col cols="6">
          <v-text-field
            v-model="justificatif"
            :rules="justificatifRules"
            label="justificatif de sortie"
            required
          ></v-text-field>
        </v-col>

        <v-col cols="2">
          <v-text-field
            v-model="montant"
            :rules="sortieRules"
            label="Montant sortie"
            required
          ></v-text-field>
        </v-col>

        <v-col cols="2">
          <v-btn
            :disabled="!valid"
            color="primary"
            class="mr-4"
            @click="validate"
          >
            Ajouter
          </v-btn>
        </v-col>
      </v-row>
    </v-form>

    <v-card-text>
      <v-row>
        <v-col cols="10" v-for="(mount, index) in listSortie" :key="index">
          <div class="text-center">
            <v-chip close @click:close="deleteCb(index)">
              {{ mount.justificatif }} => {{ mount.montant }}€
            </v-chip>
          </div>
        </v-col>
      </v-row>
    </v-card-text>

    <v-card-actions>
      <v-btn color="primary" class="mr-4" @click="back()" text> retour </v-btn>
      <v-btn color="primary" class="mr-4" @click="confirm()"> Confirmer </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    montant: "",
    justificatif: "",
    sortieRules: [
      (v) => !!v || "un montant est requis",
      (v) => (v && v > 0) || "Le montant doit être supérieur à 0€",
    ],
    justificatifRules: [(v) => !!v || "un n° est requis"],
    listSortie: [],
  }),
  methods: {
    back() {
      this.$emit("back");
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.listSortie.push({
          justificatif: this.justificatif,
          montant: this.montant,
        });
        this.$refs.form.reset();
      }
    },
    deleteCb(index) {
      this.listSortie.splice(index, 1);
    },
    confirm() {
      const message = `Voulez vous vraiment confirmer le montant à ${this.total} € ?`;
      var res = confirm(message);
      if (res) {
        var Sortie = {
          listSortie: this.listSortie,
          total: this.total,
        };
        this.$refs.form.reset();
        return this.$emit("dataSortie", Sortie);
      }
    },
  },
  computed: {
    total() {
      var total = 0;
      if (this.listSortie.length > 0) {
        this.listSortie.forEach((item) => (total += parseFloat(item.montant)));
      }
      return total;
    },
  },
};
</script>