<template>
  <v-card class="mb-12" elevation="0">
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-text-field
        v-model="journalCaisse.cb"
        :rules="Rules"
        label="CB"
        required
      ></v-text-field>
      <v-text-field
        v-model="journalCaisse.cbsc"
        :rules="Rules"
        label="CB sans contact"
        required
      ></v-text-field>
      <v-text-field
        v-model="journalCaisse.cheque"
        :rules="Rules"
        label="Cheque"
        required
      ></v-text-field>
      <v-text-field
        v-model="journalCaisse.espece"
        :rules="Rules"
        label="Espece"
        required
      ></v-text-field>
      <v-text-field
        v-model="journalCaisse.sortieCaisse"
        :rules="Rules"
        label="Sortie de caisse"
        required
      ></v-text-field>
      <v-btn color="primary" class="mr-4" @click="back()" text> retour </v-btn>
      <v-btn :disabled="!valid" color="primary" class="mr-4" @click="validate">
        Confirmer
      </v-btn>
    </v-form>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    journalCaisse: {
      cb: "",
      cbsc: "",
      cheque: "",
      espece: "",
      sortieCaisse: "",
    },
    Rules: [
      (v) => !!v || "Merci de compléter le champ",
      (v) => (v && v >= 0) || "le champs n'autorise que des chiffres",
    ],
  }),

  methods: {
    back() {
      this.$emit("back");
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.journalCaisse.total = this.total();
        this.$emit("dataJournal", this.journalCaisse);
        //this.$refs.form.reset();
      }
    },
    total() {
      return (
        parseInt(this.journalCaisse.cb) +
        parseInt(this.journalCaisse.cbsc) +
        parseInt(this.journalCaisse.cheque) +
        parseInt(this.journalCaisse.espece) -
        parseInt(this.journalCaisse.sortieCaisse)
      );
    },
    reset() {},
    resetValidation() {
      this.$refs.form.resetValidation();
    },
  },
};
</script>