<template>
  <v-card>
    <v-card-title>
      Montant Total de vos CB :
      <v-chip class="ml-2">{{ total }} €</v-chip>
    </v-card-title>

    <v-form ref="form" v-model="valid" lazy-validation>
      <v-row>
        <v-col cols="1">
          <v-text-field
            v-model="montant"
            :rules="cbRules"
            label="Montant CB"
            required
          ></v-text-field>
        </v-col>
        <v-col cols="2">
          <v-select
            v-model="encaissement"
            :rules="selectRule"
            :items="items"
            label="mode encaissement"
          ></v-select>
        </v-col>
        <v-col cols="3" v-if="encaissement == 'sur facture'">
          <v-text-field
            v-model="justificatif"
            :rules="selectRule"
            label="N°Facture ou Nom Prénom + mail + tel"
            required
          ></v-text-field>
        </v-col>

        <v-col cols="2">
          <v-btn
            :disabled="!valid"
            color="primary"
            class="mr-4"
            @click="validate"
          >
            Ajouter
          </v-btn>
        </v-col>
      </v-row>
    </v-form>

    <v-card-text>
      <v-row>
        <v-col cols="3" v-for="(cb, index) in listCb" :key="index">
          <div class="text-center">
            <v-chip close @click:close="deleteCb(index)">
              {{ cb.montant }} € - {{ cb.encaissement }}
              <div v-if="cb.justificatif">- {{ cb.justificatif }}</div>
            </v-chip>
          </div>
        </v-col>
      </v-row>
    </v-card-text>

    <v-card-actions>
      <v-btn color="primary" class="mr-4" @click="back()" text> retour </v-btn>
      <v-btn color="primary" class="mr-4" @click="confirm()"> Confirmer </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    items: ["en caisse", "sur facture"],
    montant: "",
    encaissement: "",
    justificatif: "",
    cbRules: [
      (v) => !!v || "un montant Cb est requis",
      (v) => (v && v > 0) || "Le montant doit être supérieur à 0€",
    ],
    selectRule: [(v) => !!v || "merci de choisir un encaissement"],
    listCb: [],
  }),
  methods: {
    back() {
      this.$emit("back");
    },

    validate() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.listCb.push({
        montant: this.montant,
        encaissement: this.encaissement,
        justificatif: this.justificatif,
      });
      this.$refs.form.reset();
    },

    deleteCb(index) {
      this.listCb.splice(index, 1);
    },

    confirm() {
      const message = `Voulez vous vraiement confirmer le montant à ${this.total} € ?`;
      var res = confirm(message);
      if (res) {
        var cb = {
          listCb: this.listCb,
          total: this.total,
          totalAcceuil: this.totalAcceuil,
          totalBureau: this.totalBureau,
        };
        this.$refs.form.reset();
        return this.$emit("dataCbSC", cb);
      }
    },
  },
  computed: {
    total() {
      var total = 0;
      if (this.listCb.length > 0) {
        this.listCb.forEach((item) => (total += parseFloat(item.montant)));
      }
      return total;
    },
    totalBureau() {
      var total = 0;
      var opBureau = this.listCb.filter(
        (item) => item.encaissement == "sur facture"
      );

      if (opBureau.length > 0) {
        opBureau.forEach((item) => {
          total += parseFloat(item.montant);
        });
      }

      return total;
    },
    totalAcceuil() {
      var total = 0;
      var opAcceuil = this.listCb.filter(
        (item) => item.encaissement == "en caisse"
      );

      if (opAcceuil.length > 0) {
        opAcceuil.forEach((item) => {
          total += parseFloat(item.montant);
        });
      }

      return total;
    },
  },
};
</script>
