<template>
  <v-container>
    <v-stepper v-model="e13" vertical>
      <v-stepper-step step="1" :complete="step1">
        Compter la caisse espece avec fond caisse inclus
      </v-stepper-step>
      <v-stepper-content step="1">
        <v-alert shaped type="error" v-if="error">
          {{ error }}
        </v-alert>
        <CaisseEspece @dataEspece="validCaisseEspece($event)" />
      </v-stepper-content>

      <v-stepper-step step="2" :complete="step2">
        Compter 300€ pour déposer 300€ Fond de Caisse
      </v-stepper-step>
      <v-stepper-content step="2">
        <v-alert shaped type="error" v-if="error">
          {{ error }}
        </v-alert>
        <FondCaisse
          @dataFondCaisse="validFondCaisse($event)"
          @back="backStep($event)"
        />
      </v-stepper-content>

      <v-stepper-step step="3" :complete="step3">
        Compter vos CB
      </v-stepper-step>
      <v-stepper-content step="3">
        <v-alert shaped type="error" v-if="error">
          {{ error }}
        </v-alert>
        <Cb @dataCb="validCb($event)" @back="backStep($event)" />
      </v-stepper-content>

      <v-stepper-step step="4" :complete="step4">
        Compter vos CB sans contact
      </v-stepper-step>
      <v-stepper-content step="4">
        <v-alert shaped type="error" v-if="error">
          {{ error }}
        </v-alert>
        <CbSansContact @dataCbSC="validCbSC($event)" @back="backStep($event)" />
      </v-stepper-content>

      <v-stepper-step step="5" :complete="step5">
        Compter vos chèques
      </v-stepper-step>
      <v-stepper-content step="5">
        <v-alert shaped type="error" v-if="error">
          {{ error }}
        </v-alert>
        <Cheque @dataCheque="validCheque($event)" @back="backStep($event)" />
      </v-stepper-content>

      <v-stepper-step step="6" :complete="step6">
        Renseigner les justificatifs de sortie
      </v-stepper-step>
      <v-stepper-content step="6">
        <v-alert shaped type="error" v-if="error">
          {{ error }}
        </v-alert>
        <SortieCaisse
          @dataSortie="validSortie($event)"
          @back="backStep($event)"
        ></SortieCaisse>
      </v-stepper-content>

      <v-stepper-step step="7" :complete="step7">
        Saisir le journal de caisse
      </v-stepper-step>

      <v-stepper-content step="7">
        <v-alert shaped type="error" v-if="error">
          {{ error }}
        </v-alert>
        <JournalCaisseVue
          @back="backStep($event)"
          @dataJournal="validJournalCaisse($event)"
        />
      </v-stepper-content>

      <v-stepper-step step="8" :complete="step8">
        Valider votre caisse
      </v-stepper-step>
      <v-stepper-content step="8">
        <v-btn color="primary" class="mr-4" @click="backStep()" text>
          retour
        </v-btn>
        <v-btn color="primary" @click="validCaisse()">
          Confirmer ma caisse
        </v-btn>
      </v-stepper-content>
    </v-stepper>
  </v-container>
</template>

<script>
import JournalCaisseVue from "../components/JournalCaisse.vue";
import CaisseEspece from "../components/CaisseEspece.vue";
import FondCaisse from "../components/FondCaisse.vue";
import Cb from "../components/Cb.vue";
import CbSansContact from "../components/CbSansContact.vue";
import Cheque from "../components/Cheque.vue";
import SortieCaisse from "../components/SortieCaisse.vue";
import apiCaisse from "../services/caisse";
export default {
  mounted() {
    let date = new Date();
    this.caisse.user = this.$store.state.user;
    this.caisse.date = date.toLocaleDateString("fr-FR");
  },
  methods: {
    validCaisseEspece(caisse) {
      this.error = "";

      if (caisse.total < 300) {
        return (this.error = "Probleme d'espece dans votre caisse -300€ ");
      }
      caisse.recette = caisse.total - 300;
      this.step1 = true;
      this.caisse.caisseEspece = caisse;

      if (caisse.recette == 0) {
        (this.step2 = true), (this.caisse.fondCaisse = caisse);
        return (this.e13 = 3);
      }

      this.e13 = 2;
    },
    validFondCaisse(fondCaisse) {
      this.error = "";
      if (fondCaisse.total < 300) {
        return (this.error = "Probleme de fond caisse -300€ ");
      }
      this.step2 = true;
      this.caisse.fondCaisse = fondCaisse;
      this.e13 = 3;
    },
    validCb(cb) {
      this.error = "";
      this.step3 = true;
      this.e13 = 4;
      this.caisse.cb = cb;
    },
    validCbSC(cb) {
      this.step4 = true;
      this.e13 = 5;
      this.caisse.cbsc = cb;
    },
    validCheque(cheque) {
      this.step5 = true;
      this.e13 = 6;
      this.caisse.cheque = cheque;
    },
    validSortie(sortie) {
      this.step6 = true;
      this.e13 = 7;
      this.caisse.sortie = sortie;
    },
    validJournalCaisse(journal) {
      this.error = "";
      console.log(journal);
      console.log(this.caisse);
      if (journal.cb != this.caisse.cb.totalAcceuil) {
        return (this.error = `Différence entre la caisse CB et le journal de : ${
          this.caisse.cb.totalAcceuil - journal.cb
        } €`);
      }
      if (journal.cbsc != this.caisse.cbsc.totalAcceuil) {
        return (this.error = `Différence entre la caisse CB sans contact et le journal de : ${
          this.caisse.cbsc.totalAcceuil - journal.cbsc
        } €`);
      }
      if (journal.cheque != this.caisse.cheque.totalAcceuil) {
        return (this.error = `Différence entre la caisse Chèque et le journal de : ${
          this.caisse.cheque.totalAcceuil - journal.cheque
        } €`);
      }

      if (journal.sortieCaisse != this.caisse.sortie.total) {
        return (this.error = `Différence entre la caisse de sortie et le journal de : ${
          this.caisse.sortie.total - journal.sortieCaisse
        } €`);
      }

      if (journal.espece > this.caisse.caisseEspece.recette) {
        return (this.error = `Manque d'espèce dans la caisse de : ${
          journal.espece - this.caisse.caisseEspece.recette
        } €`);
      }

      if (journal.espece > this.caisse.caisseEspece.recette) {
        this.caisse.surPlusEspece = true;
      } else {
        this.caisse.surPlusEspece = false;
      }

      this.step7 = true;
      this.e13 = 8;
      this.caisse.journalCaisse = journal;
    },
    validCaisse() {
      apiCaisse
        .createCaisse(this.caisse)
        .then((res) => {
          this.$router.push({ name: "Pdf", params: { id: res.data.id } });
        })
        .catch((err) => console.log(err));
    },
    backStep() {
      this.e13--;
    },
  },
  components: {
    JournalCaisseVue,
    CaisseEspece,
    Cb,
    FondCaisse,
    CbSansContact,
    Cheque,
    SortieCaisse,
  },
  data() {
    return {
      e13: 1,
      step1: false,
      step2: false,
      step3: false,
      step4: false,
      step5: false,
      step6: false,
      step7: false,
      step8: false,
      caisse: {},
      error: "",
    };
  },
};
</script>
